.app-header {
  background-color: #4CAF50;
  color: white;
  padding: 1.5rem 3rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.25rem;
}

.logo {
  font-size: 2rem;
  font-weight: bold;
  text-decoration: none;
  color: white;
}

.nav-buttons {
  display: flex;
  gap: 1rem;
}

.nav-button {
  background-color: white;
  color: #4CAF50;
  padding: 0.5rem 1.25rem;
  text-decoration: none;
  border-radius: 6px;
  font-weight: bold;
  transition: background-color 0.2s, color 0.2s;
}

.nav-button:hover {
  background-color: #388E3C;
  color: white;
}

.auth-form {
  max-width: 400px;
  margin: 2rem auto;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  text-align: center;
}
.auth-form input,
.auth-form button {
  padding: 0.75rem;
  font-size: 1rem;
}
